import './App.css';
import RenderInputs from './components/RenderInputs';
import { Formik } from 'formik';
import { useState } from 'react';


function App() {
  const [inputs, setInputs] = useState([
    'cc_number',
    'cc_expiry_date',
    'cvv',
    'cc_cardholder',
  ])
  const createCVVInput = () => setInputs([...inputs, 'cvv'])

  window.createCVVInput = () => createCVVInput()

  return (
    <div className="App">
      <Formik
        initialValues={[]}
        onSubmit={props => null}
      >
        {({
          handleSubmit,
          ...formikprops
        }) => (
          <RenderInputs
            formikprops={formikprops}
            checkoutConfig={null}
            inputs={inputs} />
        )}
      </Formik>
    </div >
  )
}

export default App;
